import { inline } from 'app/utils/StylesUtils';
import * as React from 'react';
import { FunctionComponent, useEffect } from 'react';
import styles from './EgElasticOpticalNetworksGuideStepStyles';
import { Fade } from '@material-ui/core';
import { SpectrumText } from 'app/Components/SpectrumText/SpectrumText';
import Typist from 'react-typist';
import { RainbowDiv } from 'app/Components/RainbowDiv/RainbowDiv';
import { SpectrumParagraphs } from 'app/Components/SpectrumParagraphs/SpectrumParagraphs';

interface IProps {
    // Props type definition
}

interface IState {
    // State type definition
}

export const EgElasticOpticalNetworksGuideStep: FunctionComponent<IProps> = (props) => {
    const initialState: IState = {
    };


    // ComponentDidMount
    useEffect(() => {
        return () => {
            //ComponentDidUnmount
        }
    }, [])

    return (
        <Fade timeout={1000} in={true}>
            <div style={inline([styles.flexStretch, styles.fullWidthContainer, styles.centeredColumn, styles.zIndex3])}>
                <SpectrumText weight={'bold'} style={inline([styles.headerText, styles.fullWidthContainer])}>
                    <Typist cursor={{
                        show: false,
                        hideWhenDoneDelay: 300
                    }} avgTypingDelay={30}>

                        Redes pticas Elsticas
                        </Typist>
                </SpectrumText>
                <RainbowDiv style={styles.rainbowDivider} />
                <SpectrumParagraphs paragraphs={[
                    "Com o objetivo de flexibilizar a utilizao do espectro luminoso e, consequentemente, reduzir o desperdcios de recursos, as Redes pticas Elsticas (EON - Elastic Optical Networks) permitem a reserva de faixa espectral de acordo com a banda passante demandada para atender a requisio de conexo, constituindo o conceito de grades (Grids) flexveis atravs da tecnologia SLICE (Spectrum-Sliced Elastic Optical Path Network).",
                    "So caracterizadas tambm como redespticas elsticas aquelas que utilizam sub-comprimentos de onda para a transmisso dasrequisies (FWDM - Flexible Optical Wavelength Division Multiplexing).",
                    "As EON utilizam a multiplexao e demultiplexao atravs de modulaes de diviso da frequncia (FDM), isso , os sinais so interpretados atravs do particionamento do espectro luminoso analisado no domnio da frequncia. Para que as grades flexveis sejam funcionais  necessrio garantir que as portadoras utilizem frequncias que possam ser sobrepostas sem perda e interferncia de sinais.",
                ]} />


                <img
                    style={inline([styles.fdmOfdmImg, styles.marginTop])}
                    src={require('../../../../Assets/Images/SPECTRUM_SAVE.png')}
                    alt="Demonstrao de Mux e Demux"
                />


                <SpectrumParagraphs paragraphs={[
                    "Com o objetivo de flexibilizar a utilizao do espectro luminoso e, consequentemente, reduzir o desperdcios de recursos, as Redes pticas Elsticas (EON - Elastic Optical Networks) permitem a reserva de faixa espectral de acordo com a banda passante demandada para atender a requisio de conexo, constituindo o conceito de grades (Grids) flexveis atravs da tecnologia SLICE (Spectrum-Sliced Elastic Optical Path Network).",
                    "So caracterizadas tambm como redespticas elsticas aquelas que utilizam sub-comprimentos de onda para a transmisso dasrequisies (FWDM - Flexible Optical Wavelength Division Multiplexing).",
                    "As EON utilizam a multiplexao e demultiplexao atravs de modulaes de diviso da frequncia (FDM), isso , os sinais so interpretados atravs do particionamento do espectro luminoso analisado no domnio da frequncia. Para que as grades flexveis sejam funcionais  necessrio garantir que as portadoras utilizem frequncias que possam ser sobrepostas sem perda e interferncia de sinais.",
                    "A utilizao e alocao de frequncias ortogonais entre si por sub-portadoras, tambm chamadas de slots, no domnio da frequncia, tambm conhecida por (OFDM - Orthogonal Frequency Division Multiplexing), tornou-se uma abordagem comumente utilizada na implementao e estudos das EON. A OFDM consiste em utilizar frequncias ortogonais entre si, para a transmisso de sinais parelelos, os quais no se interferem."
                ]} />

                <img
                    style={inline([styles.fdmOfdmImg, styles.marginTop])}
                    src={require('../../../../Assets/Images/FDM_OFDM.png')}
                    alt="Demonstrao de Mux e Demux"
                />

                <SpectrumText style={inline([styles.fullWidthContainer, styles.marginTop])} size={'h3'} weight={'semibold'}>
                    Formatos de Modulao
                    </SpectrumText>
                <RainbowDiv style={styles.rainbowDivider} />
                <SpectrumParagraphs paragraphs={[
                    "Para que um sinal seja interpretado por uma sub-portadora,  necessrio que um formato de modulao o seja atrelado. Os formatos de modulao definem como os sinais devem ser decompostos para que a extrao de informaes possa ocorrer. Isso , uma sub-portadora estar operando na em uma frequncia definida, e utilizar um formato de modulao definido para decodificar os sinais recebidos.",
                    "A decodificao pode ser realizada atravs da fase, amplitude ou frequncia da onda recebida. Devido ao fato das EON multiplexarem e demultiplexarem o sinal atravs do domnio da frequncia (FDM e OFDM), somente as decodificaes por amplitude e fase so utilizadas.",
                    "O formato de modulao mais simples e comum em telecomunicaes  o (BPSK - Binary Phase Shift Keying). A decodificao  realizada atravs do deslocamento de fase das ondas do sinal, e possibilita a transmisso de no mximo um bit por smbolo, isso , a cada sinal recebido, s receber um valor 0 ou 1",
                    "Outro formato de modulao comumente utilizado  o (QPSK Quadrature Phase Shift Keying), que desloca a fase dos sinais enviados e recebidos a cada 90 graus, iniciando no primeiro quadrante aos 45 graus, sendo assim,  capaz de transmitir dois bits por smbolo. Isso , se uma sub-portadora for alocada utilizando o QPSK como formato de modulao, e outra sub-portadora utilizando o BPSK como formato de modulao, o QPSK possibilitaria o dobro de dados transmitidos que o BPSK em um mesmo intervalo de tempo, utilizando os mesmos recursos pticos.",
                ]} />

                <img
                    style={inline([styles.fdmOfdmImg, styles.marginTop])}
                    src={require('../../../../Assets/Images/MODULATIONS.png')}
                    alt="Demonstrao de Mux e Demux"
                />
                <SpectrumText weight={'semibold'} size={'b17'} >
                    {"Embora existam formatos de modulao que carreguem mais bits por smbolo, como o 8QAM e o 64QAM, ao se tratar de Redes pticas Elsticas no  possvel utiliz-los em todas as situaes. Devido a grande quantidade de bits carregados atravs de um nico smbolo, e em consequncia  grandes distncias entre nodos e possveis interferncias de sinal, tais formatos de modulao podem resultar em dados corrompidos ou perda de informao."}
                </SpectrumText>

                <SpectrumText style={inline([styles.fullWidthContainer, styles.marginTop])} size={'h3'} weight={'semibold'}>
                    O problema do Roteamento, Atribuio de espectro e Modulao (RSA e RMLSA)
                </SpectrumText>
                <RainbowDiv style={styles.rainbowDivider} />
                <SpectrumParagraphs paragraphs={[
                    "Assim como as WDM, para que uma conexo seja estabelecida com sucesso em uma EON,  necessrio garantir uma ou mais rotas que satisfaam a requisio, conhecido tambm como o problema de roteamento.",
                    "Diferente das WDM,  necessrio alocar um caminho ptico onde as sub-portadoras, ou slots, sejam adjacentes ou subsequentes entre si. Isso se deve ao fato da utilizao da diviso ortogonal da frequncia (OFDM), na qual as frequncias vizinhas devem operar de forma ortogonal  frequncia utilizada, de forma a garantir a sobreposio correta. O problema do roteamento em conjunto com a atribuio de slots adjacentes em um caminho ptico  chamado de O Problema do Roteamento e Atribuio de Espectro (RSA - Routing and Spectrum Assignment).",
                    "Ao utilizar diferentes modulaes em diferentes caminhos pticos, o problema da modulao tambm deve ser resolvido.  necessrio escolher a melhor forma de modulao em que a conexo se encaixa, utilizando como parmetro a distncia entre nodos. Assim,  possvel calcular a quantidade de sub-portadoras necessrias para estabelecer a conexo utilizando a modulao atribuda. O RSA em conjunto com o problema da modulao  chamado de O problema do Roteamento, Atribuio de espectro e Modulao (RMLSA - Routing, Modulation and Spectrum Assignment)."
                ]} />

            </div>
        </Fade>
    );
};
