import * as React from 'react';
import { SpectrumText } from 'app/Components/SpectrumText/SpectrumText';
import { faSeedling, faTruckLoading, faPoll, faUserCheck, faCheckCircle, faCross, faWindowClose, faSortNumericUp, faPhoneSlash, faBarcode, faStream, faExclamationTriangle, faBraille } from '@fortawesome/free-solid-svg-icons';
import { Colors } from 'app/Theme';
import { getGuideContainerStyle } from 'app/utils/GuideUtils';
import { SpectrumGuideStep } from 'app/Components/SpectrumGuideStep/SpectrumGuideStep';
import { simulationConfigurationStoreContext } from '../../../Store/SimulationConfigurationStore'
import { inline } from 'app/utils/StylesUtils';
import styles from './ResultsScreenStyles'
import { FragmentationExample } from './FragmentationExample/FragmentationExample';
export const steps = [
  {
    selector: ".summaryGeneral",
    content: () => (<SpectrumGuideStep
      icon={faPoll}
      title={'Resultados Gerais'}
      content={[
        "Aqui sero apresentados os resultados gerais da simulao."
      ]} />),
    style: getGuideContainerStyle()
  },
  {
    selector: ".summaryGeneralNumberResults",
    content: () => (<SpectrumGuideStep
      icon={faSortNumericUp}
      title={'Resultados Numricos'}
      content={[
        "Os resultados numricos mostrados aqui so relacionados  todas as iteraes, de todos os ciclos.",
      ]}
      renderExtraComponent={() => {

        const configStore = React.useContext(simulationConfigurationStoreContext)

        return <div>
          <div style={inline([styles.fullWidthContainer, styles.text13, styles.primaryText, styles.regularText])} color={'white'}>
            {"Por exemplo, para o resultado de Total de chamadas, se temos\u00a0"}
            <strong >
              {configStore.generalConfiguration.simulationCycles}
            </strong>
            {"\u00a0Sementes,\u00a0"}
            <strong >
              {configStore.generalConfiguration.iterations}
            </strong>
            {"\u00a0Iteraes por ciclo e\u00a0"}
            <strong >
              {configStore.classesConfiguration.callsNumber}
            </strong>
            {"\u00a0Chamadas por iterao, teremos\u00a0"}
            <strong >
              {configStore.generalConfiguration.iterations * configStore.generalConfiguration.simulationCycles * configStore.classesConfiguration.callsNumber}
            </strong>
            {"\u00a0Chamadas no total."}
          </div>
        </div>
      }}
    />
    ),
    style: getGuideContainerStyle()
  },
  {
    selector: ".summaryGeneralNumberResultsSuccessCalls",
    content: () => (<SpectrumGuideStep
      icon={faCheckCircle}
      title={'Chamadas Atendidas'}
      content={[
        "As chamadas atendidas representam todas as requesies que tiveram sucesso ao serem transmitidas, do incio ao fim, no tempo esperado, utilizando a banda larga previamente definida.",
      ]}
    />
    ),
    style: getGuideContainerStyle()
  },
  {
    selector: ".summaryGeneralNumberResultsBlockedCalls",
    content: () => (<SpectrumGuideStep
      icon={faPhoneSlash}
      title={'Chamadas Bloqueadas'}
      content={[
        "As chamadas bloqueadas representam todas as requisies de chamadas que foram rejeitadas por falta de recurso ptico para que pudessem ser alocadas corretamente.",
      ]}
    />
    ),
    style: getGuideContainerStyle()
  },
  {
    selector: ".summaryGeneralChartResults",
    content: () => (<SpectrumGuideStep
      icon={faPoll}
      title={'Resultados Ilustrados'}
      content={
        [
          "Aqui so apresentados grficos contendo resultados em relao  carga (Iterao) simulada.",
          "A Probabilidade de Bloqueio caracteriza a porcentagem de chamadas que foram bloqueadas durante  simulao.",
          "Neste contexto, a Probabilida de Bloqueio mdia  mostrada para cada carga simulada. O intervalo de confiana tambm  ilustrado (Linha vertical rosa entre elementos do grfico). A mdia, para cada carga,  calculada atravs do resultado obtido  cada semente.",
          "A Banda Bloqueada representa o somatrio de banda larga, em Gigabytes, de todas as conexes bloqueadas.",
          "Por ltimo,  apresentado uma breve comparao entre a proporo de chamadas bloqueadas por classe de conexo.",
        ]
      }
    />
    ),
    style: getGuideContainerStyle()
  },
  {
    selector: ".summaryLoadResults",
    content: () => (<SpectrumGuideStep
      icon={faPoll}
      title={'Resultados Por Carga'}
      content={
        [
          "De forma a possibilitar uma anlise mais detalhada,  possvel filtrar os resultados  cada carga. Todos os resultados presentes na seo de resultados gerais sero apresentados aqui tambm.",
          "A grande diferna  os grficos faro uma anlise separada por ciclos (Sementes), ou seja, o Eixo X dos grficos sero as sementes utilizadas na simulao.",
          "Para filtrar os resultados, basta selecionar uma das cargas simuladas."
        ]
      }
    />
    ),
    style: getGuideContainerStyle()
  },
  {
    selector: ".summaryLoadResultsSelectLoad",
    content: () => (<SpectrumGuideStep
      icon={faPoll}
      title={'Resultados Por Carga'}
      content={
        [
          "Para filtrar os resultados, basta selecionar uma das cargas simuladas."
        ]
      }
    />
    ),
    style: getGuideContainerStyle()
  },

  {
    selector: ".summaryClassResults",
    content: () => (<SpectrumGuideStep
      icon={faStream}
      title={'Resultados Por Classe'}
      content={
        [
          " possvel ainda filtrar por Classe de Conexo, ao selecionar uma das classes configuradas."
        ]
      }
    />
    ),
    style: getGuideContainerStyle()
  },
  {
    selector: ".summaryCriticalEvents",
    content: () => (<SpectrumGuideStep
      icon={faExclamationTriangle}
      title={'Eventos Crticos'}
      content={
        [
          "Por ltimo,  possvel visualizar o estado dos enlaces da topologia em eventos crticos.",
          "No SPECTRUM, eventos crticos so caracterizados por um aumento significativo na probabilidade de bloqueio ou fragmentao da topologia em uma das interaes.",
          "A Visualizao do estado dos enlaces em momentos de eventos crticos  crucial para entender se as chamadas esto sendo distribudas de forma inteligente e eficaz entre os slots de cada enlace."
        ]
      }
    />
    ),
    style: getGuideContainerStyle()
  },
  {
    selector: ".summaryFragmentation",
    content: () => (<SpectrumGuideStep
      icon={faBraille}
      title={'Fragmentao'}
      content={
        [
          "Devido  partio do espectro luminoso atravs de pequenos sub-portadoras, a possibilidade de alocao de requisies de pequenas e grandes taxas de transmisso se tornou vivel.",
          "Ao remover conexes com poucos slots subsequentes, seguidos de slots de outras conexes com maior nmero de sub-portadoras, o espectro luminoso pode ser fragmentado ao longo do tempo."
        ]
      }
      renderExtraComponent={() => <FragmentationExample />}
    />
    ),
    style: getGuideContainerStyle()
  },
];
