import { inline } from 'app/utils/StylesUtils';
import * as React from 'react';
import { FunctionComponent, useEffect } from 'react';
import styles from './EGOpticalNetworksGuideStepStyles';
import { SpectrumScreen } from 'app/Components/SpectrumScreen/SpectrumScreen';
import { Waves } from 'app/Components/Waves/Waves';
import { SpectrumText } from 'app/Components/SpectrumText/SpectrumText';
import { RainbowDiv } from 'app/Components/RainbowDiv/RainbowDiv';
import Typist from 'react-typist';
import { Fade } from '@material-ui/core';
import { SpectrumParagraphs } from 'app/Components/SpectrumParagraphs/SpectrumParagraphs';

interface IProps {
    // Props type definition
}

interface IState {
    // State type definition
}

export const EGOpticalNetworksGuideStep: FunctionComponent<IProps> = (props) => {
    const initialState: IState = {
    };


    // ComponentDidMount
    useEffect(() => {
        return () => {
            //ComponentDidUnmount
        }
    }, [])

    return (

        <Fade timeout={1000} in={true}>
            <div style={inline([styles.flexStretch, styles.fullWidthContainer, styles.centeredColumn, styles.zIndex3])}>
                <SpectrumText weight={'bold'} style={inline([styles.headerText, styles.fullWidthContainer])}>
                    <Typist cursor={{
                        show: false,
                        hideWhenDoneDelay: 300
                    }} avgTypingDelay={30}>

                        Redes pticas
                </Typist>
                </SpectrumText>
                <RainbowDiv style={styles.rainbowDivider} />
                <SpectrumParagraphs paragraphs={[
                    'As Redes pticas surgiram com o propsito de ampliar a disponibilidade de recursos e a velocidade de transmisso de dados. So consideradas solues atraentes para suportar o rpido crescimento da Internet.',
                    'Nestas redes, utiliza-se o meio ptico para transmisso de dados, sendo assim altas taxas de transmisso de dados so atingidas atravs de fibras pticas, as quais so constitudas majoritariamente de vidro.'
                ]} />

                <SpectrumText style={inline([styles.fullWidthContainer, styles.marginTop])} size={'h3'} weight={'semibold'}>
                    MUX e DEMUX
                </SpectrumText>
                <RainbowDiv style={styles.rainbowDivider} />
                <SpectrumParagraphs paragraphs={[
                    "As Redes pticas so capazes de transmitir diversas e distintas conexes em uma nica fibra ptica, atravs da paralelizao e compartilhamento do espectro luminoso entre uma ou mais requisies. Para que isso seja possvel,  necessrio multiplexar os sinais eletrnicos que sero enviados  uma Onda Portadora.",
                    "Por definio, os dispositivos Multiplexadores tem como objetivo converter dados de vrias entradas em uma nica sada, utilizando uma estratgia de modulao do sinal. O processo inverso  denominado Demultiplexao, realizado por dispositivos Demultiplexadores.",
                    "O formato de modulao e as estratgias de converso, seja no domniodo tempo (TDM - Time-Division Multiplexing), da frequncia (FDM - Frequency-Division Multiplexing) ou atravs de diferentes comprimentos de onda (WDM - Wavelenght-Division Multiplexing) dos sinais podem variar dependendo da tecnologia utilizada."
                ]} />

                <img
                    style={inline([styles.muxDemuxImg, styles.marginTop])}
                    src={require('../../../../Assets/Images/MUX_DEMUX.png')}
                    alt="Demonstrao de Mux e Demux"
                />

                <SpectrumText style={inline([styles.fullWidthContainer, styles.bigMarginTop])} size={'h3'} weight={'semibold'}>
                    Redes pticas WDM
                        </SpectrumText>
                <RainbowDiv style={styles.rainbowDivider} />
                <SpectrumParagraphs paragraphs={[
                    "As Redes pticas por multiplexao de comprimento de onda (Wavelength Division Multiplexing- WDM) permitem a diviso do espectro luminoso presente uma nica fibra em diferentes canais, cada um sendo transmitido atravs de um comprimento de onda que no se sobrepem com os demais.",
                    "Ou seja, o critrio utilizado para realizar o processo de multiplexao (Juno), e demultiplexao (Separao)  o comprimento de onda de cada sinal recebido. Os comprimentos de onda alocados so espaados, comumente, em 50hz no domnio da frequncia, possibilitando a transmisso de at 100 (Gbps - Gigabits por segundo) por canal.",
                    "Em uma rede WDM, uma conexo  provisionada sobre um caminho ptico, o qual  estabelecido atravs da resoluo do problema de Roteamento e Alocao de Comprimento de Onda (RWA - Routing and Wavelength Assignment), o qual consiste em encontrar uma rota que satisfaa a conexo entre dois nodos da topologia, bem como a distribuio de comprimentos disponveis de cada em enlace."
                ]} />
            </div>
        </Fade>
    );
};
