import * as React from 'react';
import { getGuideContainerStyle } from '../../utils/GuideUtils';
import { SpectrumGuideStep } from '../../Components/SpectrumGuideStep/SpectrumGuideStep';
import { LoadExample } from '../../Components/LoadExample/LoadExample';
import { SpectrumText } from 'app/Components/SpectrumText/SpectrumText';
import { faSeedling, faTruckLoading, faPhone, faLayerGroup, faBalanceScale } from '@fortawesome/free-solid-svg-icons';
import { Colors } from 'app/Theme';
import { inline } from 'app/utils/StylesUtils';
import styles from './ClassesConfigurationScreenGuideStyles'

export const steps = [
    {
        selector: ".callsGuide",
        content: () => (<SpectrumGuideStep
            icon={faPhone}
            title={'Chamadas'}
            content={[
                "O nmero de chamadas representa a quantidade de requisies de chamadas simuladas na topologia, a cada instncia de simulao. Isto , para cada semente e suas respectivas cargas, sero realizadas o nmero de chamadas configurado neste campo.  importante lembrar que o nmero de chamadas  proporcional ao tempo de simulao."
            ]} />),
        style: getGuideContainerStyle()
    },
    {
        selector: ".classes",
        content: () => (<SpectrumGuideStep
            icon={faLayerGroup}
            title={'Classes'}
            content={[
                "As classes representam a configurao da Qualidade de Servio (QoS) das chamadas simuladas.  possvel configurar aqui, informaes como a durao mdia e banda mdia requisitada de cada conexo. ",
                "Ao arrastar uma classe para o topo ou para baixo, estar configurando sua prioridade na simulao. Quanto mais ao topo, mais importante, ou prioritria a classe representar. \n Obs: Esta funcionalidade no afetar os resultados da simulao nesta verso (Funcionalidade Futura).",
                // " possvel tambm habilitar a degradao de servio no momento do estabelecimento, isto , tornar a conexo flexvel e adptvel aos recursos disponveis.",
                // "Ao habilitar a degradao de servio, suas conexes so menos suscetveis ao bloqueio, uma vez em que podem ser estabelecidas com menor banda requisitada, ou at mesmo so tolerantes  espera."
            ]}
        />),

        style: getGuideContainerStyle()
    },
    {
        selector: ".classesHowTo",
        content: () => (<SpectrumGuideStep
            icon={faLayerGroup}
            title={'Classes - Modo de Utilizao'}
            content={[
                'A) Para configurar uma classe, basta pressionar o cone de edio, no canto direito de cada classe.',
                'B) Para deletar uma classe de forma rpida, basta pressionar o cone de lixeira.',
                'C) Para configurar a prioridade entre classes, arrast-la para a posio desejada. Quando mais ao topo a classe estiver, maior  a sua prioridade (Funcionalidade futura).',
            ]}
        />),

        style: getGuideContainerStyle()
    },
    {
        selector: ".classesFrequency",
        content: () => (<SpectrumGuideStep
            icon={faBalanceScale}
            title={'Proporo'}
            content={[
                'Aqui ser possvel configurar a proporo das classes em cada ciclo da simulao. Isso , de 100% das conexes que sero simuladas, qual ser a porcentagem/proporo de conexes de uma determinada classe.',
                "Em outras palavras, aqui ser definido a distribuio das porcentagens entre as classes configuradas.",
                "Para distrubuir a porcentagem, basta arrastar o componente deslizante de uma das classes escolhidas."
            ]}
        />),

        style: getGuideContainerStyle()
    },
];
