import * as React from 'react';
import { getGuideContainerStyle } from '../../utils/GuideUtils';
import { SpectrumGuideStep } from '../../Components/SpectrumGuideStep/SpectrumGuideStep';
import { LoadExample } from '../../Components/LoadExample/LoadExample';
import { SpectrumText } from 'app/Components/SpectrumText/SpectrumText';
import { faSeedling, faTruckLoading } from '@fortawesome/free-solid-svg-icons';
import { Colors } from 'app/Theme';

export const steps = [
  {
    selector: ".seedsGuide",
    content: () => (<SpectrumGuideStep
      icon={faSeedling}
      title={'Sementes'}
      content={[
        "As sementes representam um nmero randmico que  utilizado para controlar as distribuies matemticas que geram a instncia de uma simulao. Quanto maior o nmero de sementes utilizadas, mais simulaes sero realizadas. Sendo assim, o nmero de sementes est diretamente relacionada ao tempo de simulao e o nvel de confiana dos resultados."
      ]} />),
    style: getGuideContainerStyle()
  },
  {
    selector: ".loadGuide",
    content:

      () => (<SpectrumGuideStep
        icon={faTruckLoading}
        title={'Carga'}
        content={[
          "A carga controla o fluxo de conexes em um determinado espao e perodo de tempo. Quanto maior for a carga, mais conexes compartilharo o espectro fsico, resultando em recursos mais escassos. Uma alta carga simula situaes do mundo real, onde milhares de conexes so abertas em um pequeno perodo de tempo."
        ]}
        renderExtraComponent={() => <div>
          <LoadExample />
          <SpectrumText color={Colors.colors.primary} size={'c13'}>
            {'Deslize a barra para simular a alterao de carga e o comportamento das conexes em um quadro de tempo.'}
          </SpectrumText>
        </div>}
      />),
    style: getGuideContainerStyle()
  },
];
